
import Account from "./components/Account.vue";
import Ipfs from "./components/Ipfs.vue";
import robonomics from "./robonomics";

export default {
  name: "App",
  components: { Account, Ipfs },
  data() {
    return {
      isReady: false
    };
  },
  async created() {
    await robonomics.run();
    this.isReady = true;
  }
};
