
export default {
  data() {
    return {
      status: "Connecting",
      online: false
    };
  },
  mounted() {
    this.getIpfsNodeInfo();
  },
  methods: {
    async getIpfsNodeInfo() {
      try {
        const ipfs = await this.$ipfs;
        this.status = "Connected";
        this.online = ipfs.isOnline();
      } catch (err) {
        this.status = `Error: ${err}`;
      }
    }
  }
};
