
import robonomics from "../robonomics";
const { Keyring } = require("@polkadot/keyring");
const util = require("@polkadot/util");
import { encodeAddress } from "@polkadot/util-crypto";
import { catFile } from "../ipfs";
import Pagination from "./Pagination";

export default {
  components: { Pagination },
  props: ["address"],
  data() {
    return {
      isLoad: false,
      log: [],
      uri: "",
      currentPage: 0
    };
  },
  created() {
    this.read();
  },
  computed: {
    account() {
      if (this.uri) {
        try {
          const k = new Keyring();
          const a1 = k.addFromUri(this.uri, {}, "ed25519");
          if (encodeAddress(this.address) === encodeAddress(a1.address)) {
            return a1;
          }
          const a2 = k.addFromUri(this.uri, {}, "sr25519");
          if (encodeAddress(this.address) === encodeAddress(a2.address)) {
            return a2;
          }
          return a1;
        } catch (error) {
          console.log(error);
        }
      }
      return null;
    },
    validateUri() {
      if (
        this.account &&
        encodeAddress(this.address) === encodeAddress(this.account.address)
      ) {
        return true;
      }
      return false;
    }
  },
  methods: {
    nextPage() {
      this.currentPage++;
    },
    prevPage() {
      this.currentPage--;
    },
    handlePage(page) {
      this.currentPage = page;
    },
    async read() {
      this.isLoad = true;
      const log = await robonomics.datalog.read(this.address);
      this.log = log.reverse().map((item, id) => {
        return [
          new Date(item[0].toNumber()).toLocaleString(),
          item[1].toHuman(),
          null,
          null,
          false,
          id
        ];
      });
      this.isLoad = false;
    },
    async readByIndex(i) {
      this.log[i][4] = true;
      try {
        this.log[i][2] = await catFile(this.log[i][1]);
        this.log[i][4] = false;
      } catch (error) {
        console.log(error);
        this.readByIndex(i);
      }
    },
    decryptByIndex(i) {
      try {
        const msg = this.decrypt(this.log[i][2]);
        try {
          this.log[i][3] = JSON.stringify(
            JSON.parse(msg.replaceAll("None", "'None'").replaceAll("'", '"')),
            null,
            4
          );
        } catch (error) {
          console.log(error);
          this.log[i][3] = msg;
        }
      } catch (error) {
        console.log(error);
      }
    },
    decrypt(encryptMessage) {
      const decryptMessage = this.account.decryptMessage(
        encryptMessage,
        this.account.publicKey
      );
      return util.u8aToString(decryptMessage);
    }
  }
};
